.top-nav .client-area-wrap {
  flex-wrap: wrap;
  order: 3;
  ul {
    padding:0;
    min-width: 260px;      
    border:0;
    li {
      margin: 0;
      a {           
        font-weight: 500;
        padding:7px 10px;
        margin-bottom: 0; 
        white-space: normal;
        display: block;
        transition: all .3s ease-in-out;
      }
    }
  }
  .dropdown-toggle::after {
    display: none;
  }
}

.top-nav {
  padding: 5px 0;
  transition: all .8s ease-in-out;
  
  .container {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    position: relative;    
    @include media-breakpoint-down(md) {
      padding: 8px 15px;
    }
  }

  ul {
    margin-left: 10px;
    @include media-breakpoint-down(sm) {
      margin-left: 0;
    }
    li {
      margin: 0 2px;     
      @include media-breakpoint-down(lg) {
        line-height:1.5;
      }
      a {
        padding: 5px;
        font-size: 15px;
        text-decoration: none;
        @include media-breakpoint-down(lg) {
         font-size:1.4rem;
        }
        @include media-breakpoint-down(md) {
          padding: 5px 0;         
        }       
        img {
          max-width: 20px;
          margin-right: 5px;
          transition: all .3s ease-in-out;
        }
      }
    }
  }

  .social-nav {
    ul{
      display:flex;
      align-items:center;
      @include media-breakpoint-down(lg) {
        margin-top:5px;
      }
      li a {
        font-size: 1.4rem;
        svg{
          height: 15px;
          width: 13px;
          margin-top:-2px;
        }
      }
    }
  }

  .header-contact {
    display: flex;
    @include media-breakpoint-down(lg) {
      flex-wrap: wrap;
      flex-direction: column;      
    }
    > ul {
      display: flex;
      @include media-breakpoint-down(lg) {
        display: flex;
        flex-direction: column;
      }

      li a {
        i {
          font-size: 18px;
          margin-right: 6px;
          @include media-breakpoint-down(lg) {
            font-size:1.4rem;          
          }
        }

        &:hover {
          text-decoration: underline;
        }
      }
    }    
  }
}


header {
  @include media-breakpoint-down(xl) {
    .btn {
      min-width: 170px;
    }
    .container{
      max-width:1070px;
    }
  }
  transition: all .8s ease-in-out;
  &.fixed {
    position: fixed;
    background: $white;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    padding: 0;
    @include shadow(-1px 1px 7px 0px rgba(0, 0, 0, 0.12));
    z-index: 99;
    .top-nav{
      padding:0;
      transition: all .8s ease-in-out;
    }
    img {      
      max-width: 90%;
      transition: all .8s ease-in-out;
      @include media-breakpoint-down(lg) {
        max-width: 100px;
        min-height: 76px;
        object-fit: contain;
      }
    }
    .navbar {
      padding: 10px 0;
      transition: all .8s ease-in-out;
    }
    .btn{    
      transition: all .8s ease-in-out;
    }
  }
}

.navbar-dark {
  .navbar-nav {
    .menu-item-has-children {
      &.nav-item {
        margin-right: 15px;
      }
    }

    .nav-item {
      margin: 0 3px;
      padding: 0 3px;

      @media(max-width:1400px) {
        margin: 0 3px;
        padding: 0 3px;
      }

      @include media-breakpoint-down(lg) {
        margin: 0 12px;
      }

      .nav-link {
        font-size: 1.6rem;
        line-height: 1.5rem;
        font-weight: $font-weight-normal;
        @include transition(all 0.3s ease-in-out);

        @media(max-width:1400px) {
          font-size: 1.5rem;
        }
        @include media-breakpoint-down(xl) {
          font-size: 1.4rem;          
        }  
        @include media-breakpoint-down(lg) {
          padding: 15px 0;
        }
      }

      &:hover {

        .nav-link {

          &::after {}
        }
      }
    }

    @include media-breakpoint-up(lg) {
      .menu-item-has-children {
        &:hover {
          .dropdown-menu {
            display: block
          }
        }
      }
    }

    .menu-item-has-children {
      .arrow-icon {
        display: none;

        @include media-breakpoint-down(lg) {
          display: block;

          &:after {
            border: 0;
            font-family: fontawesome;
            content: "\f107";
            position: absolute;
            right: -5px;
            font-size: 18px;
            top: 10px;
            @include transition(all 0.2s ease-in-out);
          }

          &.arrow-flip {
            &:after {
              transform: rotate(-180deg);
            }
          }
        }
      }
    }
  }

  .btn {
    margin-left:20px;
    padding: 10px 10px;
    font-size: 1.6rem;   
    min-width: 131px;
    @include transition(all 0.3s ease-in-out);
    @include media-breakpoint-down(xl) {
      padding: 6px 10px;      
      margin: 10px 0 0 15px;
    }
  }

  img {
    @include transition(all 0.3s ease-in-out);
    @media(max-width:1400px) {
      max-width: 150px;
    }
  }

  .dropdown-menu {
    @include shadow(-1px 1px 7px 3px rgba(0, 0, 0, 0.12));
    border: 0;
    font-size: 14px;
    width: 180px;
    padding: 0;

    @include media-breakpoint-down(lg) {
      box-shadow: unset;
      width: 100%;
      border-radius: 0;
      border-left: 0;
      border-right: 0;
      margin: 10px 0;
    }

    .nav-item {
      margin: 0px;
      padding: 0;

      &.menu-item-has-children {
        a {
          &:after {
            display: block;
            content: "\f105";
            font-family: fontawesome;
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);

            @include media-breakpoint-down(lg) {
              display: none;
            }
          }
        }

        .dropdown-menu {
          position: absolute;
          left: 100%;
          top: 0;

          @include media-breakpoint-down(lg) {
            position: relative;
            left: 0;
            padding-left: 15px;
          }

          a {
            &:after {
              display: none;
            }
          }
        }
      }

      .dropdown-item {
        font-weight: $font-weight-medium;
        padding: 13px 15px;

        @include media-breakpoint-down(lg) {
          padding: 7px 15px;
        }

        margin-bottom:0px;
        white-space: normal;

        &:hover {
          @include transition(all 0.3s ease-in-out);
        }
      }

      &:first-child .dropdown-item {
        &:hover {
          border-top-left-radius: 2.5px;
          border-top-right-radius: 2.5px;
        }
      }

      &:last-child .dropdown-item {
        &:hover {
          border-bottom-left-radius: 2.5px;
          border-bottom-right-radius: 2.5px;
        }
      }
    }
  }



  .dropdown-toggle::after {
    border: 0;
    font-family: 'fontawesome';
    content: '\f107';
    position: absolute;
    @include transition(all 0.3s ease-in-out);
    right: -5px;
    font-size: 17px;

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  .dropdown-toggle.show::after {
    @include media-breakpoint-down(lg) {
      transform: rotate(180deg);
    }
  }

  .navbar-toggler {
    border: 0;
    position: relative;
    padding-top: 8px;
    margin-top: 8px;
    &:after {
      position: absolute;
      width: 24px;
      height: 2px;
      border-radius: 30px;
      content: "";
      right: 7px;    
      top: 0px;
    }
    .navbar-toggler-icon {
      position: relative;
      width: 24px;
      background: transparent;


      &::before,
      &::after {
        position: absolute;
        width: 100%;
        height: 2px;
        border-radius: 30px;
        content: '';
        left: 0;
      }

      &::after {
        width: 100%;
        top: 8px;
        right: 0;
        left: unset;
      }
    }
  }

  .offcanvas {
    border: 0;

    .offcanvas-header {
      .btn-close-white {
        opacity: 1;
        filter: unset;
        margin: 10px;
      }
    }
  }    
  .client-area-wrap {
    flex-wrap: wrap;
    order: 3;
    ul {
      padding:0;
      min-width: 260px;      
      border:0;
      li {
        margin: 0;
        a {           
          font-weight: 500;
          padding:7px 10px;
          margin-bottom: 0; 
          white-space: normal;
          display: block;
          transition: all .3s ease-in-out;
        }
      }
    }
    .dropdown-toggle{
      font-size: 1.4rem;
      margin-left: 12px;
      a{
        text-decoration:none;
          &::after {
          display: none;
        }
      }
    } 
  }
}

.navbar {
  padding: 30px 0 30px;
  @include transition(0.5s ease-in-out all);

  @include media-breakpoint-down(lg) {
    padding: 10px 0 10px;

    img {
      max-width: 100px;
      min-height: 76px;
      object-fit: contain;
    }
  } 

  .navbar-collapse {
    @include media-breakpoint-down(lg) {
      position: fixed;
      top: 199px;
      right: -100%;
      height: 90vh;
      z-index: 11;
      width: 350px;
      padding: 20px 15px 140px 15px;
      @include transition(all 0.3s ease-in-out);
      display: block;
      overflow-y: auto;
      @at-root .fixed &{
        top: 189px;
      }
    }
  }
}

.menu-open {
  overflow: hidden;

  .navbar {
    .navbar-collapse {
      right: 0;
    }

    .navbar-toggler {
      &:after {
        display: none;
      }
      .navbar-toggler-icon {
        &:before {
          transform: rotate(-45deg);
        }

        &:after {
          top: 0;
          width: 100%;
          transform: rotate(45deg);
        }
      }
    }
  }
}