.video-wrap {
    position: relative;
    overflow: hidden;
    height: 100%;

    &:before {
        content: '';
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
    }

    i.fa {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 80px;
        line-height: 68px;
        box-shadow: 0px 0px 14px 5px;
        border-radius: 100%;
        z-index: 9;

        @include media-breakpoint-down(lg) {
            font-size: 50px;
            line-height: 40px;
        }
    }
}

.featured-video-section {
    .video-wrap {
        border-radius: 20px;
        height: 400px;

        img {
            height: 100%;
            object-fit: cover;
        }

        @include media-breakpoint-down(lg) {
            height: 300px;
        }
    }

    .video-content {
        @include media-breakpoint-up(lg) {
            padding: 0 70px;
        }

        @include media-breakpoint-down(lg) {
            padding: 20px;
        }
    }
}

