.infobox-section {
  padding: 280px 0 40px;
  overflow: hidden;
  @media(max-width:1440px){
    padding: 180px 0 40px
  }
  @include media-breakpoint-down(xl){
    padding:100px 0 40px
  }
  @include media-breakpoint-down(lg){
    padding:100px 0 50px
  }  
  .shape-blue {
    position: absolute;
    top: 0;
    z-index: -1;
    width:110%;    
  }
  @include media-breakpoint-down(lg){ 
    .shape-color {
      position: absolute;
      width: 100%;
      z-index: -1;
      height: 80%;
      top:20%;
    }
  }
  @include media-breakpoint-down(md){ 
    .shape-color {
      height: 96%;
      top:6%;
    }
  }
  .infobox-warp{
    .row{
      justify-content:center;
      .info-box {
        box-shadow: 0px 12px 16px rgba(40, 41, 61, 0.0364);
        border-radius: 12px;
        padding: 48px 40px;
        @include transition(0.3s ease-in-out all);
        .info-description{ 
          font-size:2.1rem;     
          margin-bottom:1rem;    
          @media(max-width:1440px){
            font-size:1.8rem;             
          }      
        }
        .btn{
          margin-top:2rem;
        }
        h4{
          margin:15px 0 20px; 
          font-size:3.1rem;     
          @media(max-width:1440px){
            font-size:2.7rem;             
          }
          @include media-breakpoint-down(md){
            font-size:3.1rem;        
          }
          font-weight:$font-weight-semibold;
        }
        @include media-breakpoint-down(md){
          box-shadow: 3px 4px 9px rgb(40 41 61 / 12%);
        }
      }
    }
  }
}

section.infobox-section.resource-infobox{
  padding:100px 0;
  @include media-breakpoint-down(lg){
    padding: 50px 0;
  }
  @include media-breakpoint-down(md){
    padding: 40px 0;    
  }
}

.checklist-infobox{
  svg.shape-light-grey {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
  }

  .shape-color {
    height: 90%;
    bottom: -30px;
    position: absolute;
    width: 100%;
    z-index: -1;
    @include media-breakpoint-down(lg){
      height:100%;      
    }
  }
}

.topics-load-more, .types-load-more {
  display: table;
  margin: 80px auto 0;
  @include media-breakpoint-down(lg){
    margin:50px auto 0;    
  }
  @include media-breakpoint-down(md){
    margin:40px auto 0;    
  }
}

.content-topic-section, .content-type-section{
  padding:150px 0;
  @include media-breakpoint-down(lg){
    padding:50px auto 0;        
  }
  @include media-breakpoint-down(md){
    padding:40px auto 0;    
  }
  .info-box{
    box-shadow: -1px 1px 16px rgb(40 41 61 / 9%);
  }
  .resources-load-more {
    display: table;
    margin: 60px auto 0;
  }
}